const BanditManchot = ({
  top = 50,
  elementsHeights,
  endedCallback = () => {},
  rollingCallback = () => {},
}) => {
  const getRandInt = (max) => {
    return Math.floor(Math.random() * max);
  };
  const a = getRandInt(3);
  const b = getRandInt(3);
  const c = getRandInt(3);

  const first = (number) => {
    console.log(number);
    switch (number) {
      case 1:
        return "C";
        break;
      case 2:
        return "F";
        break;
      case 0:
        return "B";
        break;

      default:
        return "7";
        break;
    }
  };
  const roll = [
    `${first(a)} X 7 C F `,
    `${first(b)} X C F 7 `,
    `${first(c)} X C F B `,
  ];
  const app = useContext(AppContext);

  const [rolling, setRolling] = useState(false);
  const [activated, setActivated] = useState(false);
  const [count, setCount] = useState(0);
  const [ended, setEnded] = useState(false);
  const handleRoll = () => {
    setRolling(true);
    setActivated(true);
    rollingCallback(true);
  };
  const rollEnd = () => {
    setRolling(false);
    rollingCallback(false);
  };
  useEffect(() => {
    //Implementing the setInterval method
    const interval = setInterval(() => {
      if (rolling) {
        setCount(count + 1);
        if (count > 2) {
          rollEnd();
        }
      }
    }, 1000);
    //Clearing the interval
    return () => clearInterval(interval);
  }, [count, rolling]);
  return (
    <div>
      <BanditManchotContainer
        top={top}
        rolling={rolling}
        result={roll}
      ></BanditManchotContainer>
      {ended ? (
        <>
          <ButtonContainer top={top + 15}>
            <RollButton
              customClass="fighterFont"
              style={{ width: "100vw" }}
              rolling={rolling}
              onClick={handleRoll}
              // subtitles={"@4each.records"}
            >
              FAUX RICHES
            </RollButton>
          </ButtonContainer>
          <ButtonContainer top={top + 70}>
            <a href={app.link} download={app.name}>
              <RollButton
                onClick={() => {
                  console.log("hello");
                }}
                rolling={rolling}
              >
                Download
              </RollButton>
            </a>
          </ButtonContainer>
        </>
      ) : (
        <></>
      )}
      {rolling ? (
        <>
          <CustomLightning
            //   style={{ top: 100 }}
            top={top + 10}
            right={0}
            opacity={100}
            rotate={55}
            src={lightning}
            alt="Logo"
          />
          <CustomLightning
            top={top + 35}
            right={3}
            opacity={100}
            rotate={115}
            src={lightning}
            alt="Logo"
          />
          <CustomLightning
            top={top + 35}
            left={0}
            opacity={100}
            rotate={-115}
            src={lightning}
            alt="Logo"
          />
          <CustomLightning
            //   style={{ top: 100 }}
            top={top + 10}
            left={0}
            rotate={-55}
            opacity={100}
            src={lightning}
            alt="Logo"
          />
        </>
      ) : (
        <></>
      )}
      <Banner></Banner>
      <BanditManchotWheel
        top={elementsHeights}
        rolling={rolling}
        activated={activated}
        endCallBack={() => {
          setEnded(true);
          endedCallback(true);
        }}
      ></BanditManchotWheel>
      <Banner></Banner>
      <ButtonContainer top={top + 70} rolling={rolling} activated={activated}>
        <RollButton rolling={rolling} onClick={handleRoll}>
          ROLL
        </RollButton>
      </ButtonContainer>
      <CustomBottomContainer></CustomBottomContainer>
    </div>
  );
};

export default BanditManchot;
