import React, { useEffect, useState } from "react";
import "./App.scss";
import { banneraltered, banneralteredwithouttext, bannerwaiting } from "./img";
import { GlitchContainer, VignetteContainer } from "./components/atoms";

import { styled } from "styled-components";
import { BanditManchot } from "./components/organism";

import banner from "../src/img/banner.jpg";
import { AppProvider } from "./providers";

const Banner = styled.span`
  // -webkit-box-shadow: inset 0px 0px 0px 10px #f00;
  // -moz-box-shadow: inset 0px 0px 0px 10px #f00;
  // box-shadow: inset 0px 0px 0px 10px #f00;
  height: 20vh;
  width: 100vw;
  // border: 3px solid red;
  display: flex;
  justify-content: center;
  align-items: center;
`;
const CustomImage = styled.img`
  height: 20vh;
  width: 100%;

  -webkit-box-shadow: inset 0 0 50px #000, inset 0 0 50px #000,
    inset 0 0 50px #000;
  -moz-box-shadow: inset 0 0 50px #000, inset 0 0 50px #000, inset 0 0 50px #000;
  box-shadow: inset 0 0 50px #000, inset 0 0 50px #000, inset 0 0 50px #000;
  repeating-linear-gradient(transparent 0, rgba(0, 0, 0, 1) $line-width/2, transparent $line-width );
  `;
const CustomLightning = styled.img`
  position: absolute;
  z-index: 100;
  animation: 4s linear 0s alternate disapear @-webkit-keyframes disapear {
    from {
      margin-left: -20%;
    }
    to {
      margin-left: 100%;
    }
  }
  @keyframes disapear {
    from {
      margin-left: -20%;
    }
    to {
      margin-left: 100%;
    }
  }
`;

function App({ data }) {
  const [loading, setLoading] = useState(true);
  const handleLoad = () => {
    setLoading(false);
  };
  useEffect(() => {
    window.addEventListener("load", handleLoad);
    return () => {
      window.removeEventListener("load", handleLoad);
    };
  }, []);
  useEffect(() => {
    const timeout = setTimeout(handleLoad, 5000);
    return () => {
      clearTimeout(timeout);
    };
  }, []);
  const [ended, setEnded] = useState(false);
  const [rolling, setRolling] = useState(false);
  const [hasRolled, setHasRolled] = useState(false);
  let classes = "glitchImage vignette";

  return (
    <AppProvider data={data}>
      <div className="App">
        {loading ? (
          <>
            <div style={{ color: "white" }}>Chargement</div>
          </>
        ) : (
          <>
            <VignetteContainer>
              {rolling || (hasRolled && !ended) ? (
                <Banner className="">
                  <GlitchContainer
                    height={20}
                    url={
                      rolling && hasRolled
                        ? bannerwaiting
                        : hasRolled && !ended
                        ? banneralteredwithouttext
                        : ended
                        ? banneraltered
                        : banner
                    }
                    glitch
                  ></GlitchContainer>
                </Banner>
              ) : (
                <Banner className="">
                  <GlitchContainer
                    height={20}
                    url={ended ? banneraltered : banner}
                  ></GlitchContainer>
                </Banner>
              )}
            </VignetteContainer>
            <BanditManchot
              endedCallback={() => {
                setEnded(true);
              }}
              rollingCallback={(state) => {
                setHasRolled(true);
                setRolling(state);
              }}
              elementsHeights={0}
              top={20}
            ></BanditManchot>
          </>
        )}
      </div>
    </AppProvider>
  );
}

export default App;
