import { useRef, useEffect, useState } from "react";
import { useFrame, useLoader } from "@react-three/fiber";
import { MeshTransmissionMaterial, Text } from "@react-three/drei";
import { TextureLoader } from "three/src/loaders/TextureLoader";
import ImagePlane from "../ImagePlane/ImagePlane";
import { MeshBasicMaterial } from "three";
import img from "./../../../../img/lightning-min.png";
import cherry from "../../../../img/cherry-min.png";
import fauxriches from "../../../../img/fauxriches-min.png";
import seven from "../../../../img/seven-min.png";
import bar from "../../../../img/bar-min.png";
import forich from "../../../../img/4r-min.png";
import * as THREE from "three";
export default function Ring({
  text,
  radius,
  height,
  segments,
  position,
  scale,
  rollDelta,
  rolling,
  activated,
  endCallBack = (ref, result) => {},
}) {
  const ref = useRef();
  const angleStep = (2 * Math.PI) / text.length;
  const textPositions = [];
  const texture = useLoader(THREE.TextureLoader, img);
  const [lightining, setLightining] = useState(0);
  const [endDone, setEndDone] = useState(false);
  useFrame((state, delta) => {
    let i = 2;
    let angle = i * angleStep;

    const x = radius * Math.cos(angle);

    const turn = 2.95;
    const value = 4 * turn;

    if (activated) {
      if (rolling && lightining < 100) {
        setLightining(lightining + delta * 5);
      } else {
        setLightining(lightining - delta * 10);
      }
      if (ref.current.rotation.x < value * (Math.PI / 2)) {
        ref.current.rotation.x +=
          ((delta * rollDelta + Math.random(10) / 100) * ((Math.PI / 2) * 10)) /
          (rolling ? 0.5 : 2);
      } else if (rolling) {
        ref.current.rotation.x = 0;
      } else if (endDone === false) {
        setEndDone(true);
        endCallBack(ref, ref.current.rotation.x);
        // window.alert("End");
      }
    }
  });

  // Calculate positions for text
  for (let i = 0; i < text.length; i++) {
    const angle = i * angleStep;
    const x = radius * Math.cos(angle);
    const z = radius * Math.sin(angle);
    textPositions.push({ x, z });
  }

  return (
    <group
      scale={scale}
      position={position}
      rotation={[-Math.PI / 2, 0, -Math.PI / 2]}
      ref={ref}
    >
      <mesh>
        <cylinderGeometry args={[radius, radius, height, segments]} />

        <meshBasicMaterial attach="material" color={"white"} />
      </mesh>
      {text.split("").map((char, index) => {
        let img = "fauxriches";
        switch (char) {
          case "C":
            img = cherry;
            break;
          case "7":
            img = seven;
            break;
          case "B":
            img = bar;
            break;
          case "F":
            img = forich;
            break;
          default:
            img = fauxriches;
            break;
        }

        return char != " " ? (
          <ImagePlane
            // <Text
            scale={1 / 2.3}
            key={index}
            position={[textPositions[index].x, 0, textPositions[index].z]}
            rotation={[0, -angleStep * index + Math.PI / 2, 0]}
            fontSize={0.3}
            lineHeight={1}
            letterSpacing={0.02}
            color="white"
            textAlign="center"
            img={img}
          />
        ) : (
          // </Text>
          ""
        );
      })}
    </group>
  );
}
