import React, { useState, useEffect } from "react";
import { styled } from "styled-components";
import { Scene, EffectRing, Ring } from "../../three";

const SceneContainer = styled.div`
  height: ${(props) => {
    return props.height;
  }}vh;
  width: 100vw;
`;

const BanditManchotWheel = ({
  result = ["7 X 7 C F ", "7 X C F 7 ", "7 X C F B "],
  activated,
  rolling,
  scale = 1.4,
  height = 1.5,
  segments = 32,
  top = 0,
  endCallBack = (status) => {},
}) => {


  const [ring1, setRing1] = useState(false);
  const [ring2, setRing2] = useState(false);
  const [ring3, setRing3] = useState(false);
  const [ended, setEnded] = useState(false);
  const handleRing1 = () => {
    setRing1(true);
  };
  const handleRing2 = () => {
    setRing2(true);
  };
  const handleRing3 = () => {
    setRing3(true);
  };
  useEffect(() => {
    if (ring1 && ring2 && ring3) {
      setEnded(true);
      endCallBack(true);
    }
    return () => {};
  }, [ring1, ring2, ring3]);
  return (
    <div>
      <SceneContainer height={top}></SceneContainer>
      <Scene>
        {rolling ? (
          <>
            <EffectRing
              rolling={rolling}
              text={result[0]}
              radius={2.2}
              thickness={1}
              rollDelta={0.01}
              scale={1.5}
              height={5.4}
              box
              segments={segments}
              position={[0, 0, 0]}
            />
          </>
        ) : (
          <></>
        )}

        <Ring
          endCallBack={handleRing1}
          activated={activated}
          rolling={rolling}
          text={result[0]}
          radius={rolling ? 2 : 2.4}
          rollDelta={0.06}
          scale={1.4}
          height={1.8}
          segments={segments}
          position={[-2.3, 0, 0]}
        />
        <Ring
          endCallBack={handleRing2}
          activated={activated}
          rolling={rolling}
          text={result[1]}
          rollDelta={0}
          radius={rolling ? 2 : 2.4}
          scale={1.4}
          height={1.8}
          segments={segments}
        />
        <Ring
          endCallBack={handleRing3}
          activated={activated}
          rolling={rolling}
          text={result[2]}
          rollDelta={0.002}
          radius={rolling ? 2 : 2.4}
          scale={1.4}
          height={1.8}
          segments={segments}
          position={[2.3, 0, 0]}
        />
      </Scene>
    </div>
  );
};

export default BanditManchotWheel;
