import React from "react";
import { styled } from "styled-components";
const CustomVignetteContainer = styled.span`
  -webkit-box-shadow: inset 10px 10px 200px 0px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: inset 10px 10px 200px 0px rgba(0, 0, 0, 0.1);
  box-shadow: inset 10px 10px 200px 0px rgba(0, 0, 0, 0.1);
`;
const VignetteContainer = ({ ...props }) => {
  return <CustomVignetteContainer {...props}></CustomVignetteContainer>;
};

export default VignetteContainer;
