import React from "react";
import { styled } from "styled-components";

const rollinColor1 = "rgba(0, 20, 20, 1), rgba(160, 0, 160, 1)";
const rollinColor2 = "rgba(0, 0, 0, 1) , purple";

const BackgroundLeft = styled.div`
  position: absolute;
  top: ${(props) => {
    return props.top;
  }}vh;
  left: 0;
  width: 44%;
  height: 60%;
  z-index: -10;
  clip-path: polygon(0 0, 100% 75%, 100% 70%, 0% 100%);
  clip-path: polygon(0 100%, 80% 77%, 80% 60%, 0 0);
  background-image: linear-gradient(
    to left,
    ${(props) => {
      return props.rolling ? rollinColor1 : rollinColor2;
    }}
  );
  background-color: red;
`;
const BackgroundRight = styled.img`
  position: absolute;
  top: ${(props) => {
    return props.top;
  }}vh;
  right: 0;
  width: 44%;
  height: 60%;
  z-index: -10;
  // clip-path: polygon(100% 75%, 100% 67%, 100% 0, 0 75%);
  clip-path: polygon(20% 77%, 100% 100%, 100% 0, 20% 60%);
  background-image: linear-gradient(
    to right,
    ${(props) => {
      return props.rolling ? rollinColor1 : rollinColor2;
    }}
  );

  background-color: green;
`;
const Background = styled.img`
  position: absolute;
  top: ${(props) => {
    return props.top;
  }}vh;
  left: 0;
  width: 100vw;
  height: 60vh;
  z-index: -11;
  background-image: radial-gradient(
    ${(props) => {
      return props.rolling ? rollinColor1 : rollinColor2;
    }}
  );
`;
const BanditManchotContainer = ({ rolling, top }) => {
  return (
    <>
      <BackgroundLeft top={top} rolling={rolling}></BackgroundLeft>
      <BackgroundRight top={top} rolling={rolling}></BackgroundRight>
      <Background top={top} rolling={rolling}></Background>
    </>
  );
};

export default BanditManchotContainer;
